import React, { useEffect } from "react";
import {Link,useSearchParams, useParams,useNavigate,} from "react-router-dom";
import {Row,Col,ListGroup,Image,Form,Button,Card,} from "react-bootstrap";
import Message from "../components/Message";
import { addToCart, removeFromCart } from "../actions/cartActions";
import { useDispatch, useSelector } from "react-redux";


function CartScreen() {
    const navigate = useNavigate();
    const { id } = useParams();

    const [searchParams, setSearchParams] = useSearchParams();
    const qty = Number(searchParams.get("qty"));

    const dispatch = useDispatch();

    const cart = useSelector((state) => state.cart);
    const { cartItems } = cart;
  

    useEffect(() => {
        if (id) {
            dispatch(addToCart(id, qty));
        }
    }, [dispatch, id, qty]);



    const removeFromCartHandler = (id) =>{
        dispatch(removeFromCart(id))
    }

    // NOTE: "/login?redirect=shipping" means that the redirect parameter 
    // substitutes the path "/login" to "/shipping"
    const checkoutHandler = () =>{
        navigate('/login?redirect=shipping');
    }


    return (
        <Row>
            <Col md={8}>
                <h1>Shopping Cart</h1>
                {cartItems.length === 0 ? (
                    <Message variant="info">
                        Your cart is empty! &nbsp;
                        <Link to="/">Go Back</Link>
                    </Message>
                ) : (
                    <ListGroup variant="flush">
                    
                        {cartItems.map((item) => (
                            <ListGroup.Item key={item.product}>
                               
                                <Row>
                                    <Col md={2}>
                                        <Image
                                            src={item.image}
                                            alt={item.name}
                                            fluid
                                        />
                                    </Col>
                                    <Col md={3}>
                                        <Link to={`/product/${item.product}`}>
                                            {item.name}
                                        </Link>
                                    </Col>
                                    <Col md={2}>
                                        <h4 className="noPadding">
                                            ${item.price}
                                        </h4>
                                    </Col>
                                    <Col sm={3} md={2}>
                                        <Form.Select
                                            
                                            size = 'sm'
                                            value={item.qty}
                                            onChange={(e) => dispatch( addToCart(  item.product, Number(e.target.value) ))}
                    
                                        >
                                           
                                            {
                                                [...Array(item?.countInStock).keys()].map((x) => (
                                                    <option key={x + 1} value={x + 1} >
                                                        {x + 1}
                                                    </option>
                                                ))
                                            }
                                        </Form.Select>
                                    </Col>
                                    <Col md={1}>
                                        <Button
                                            type='button'
                                            variant='light'
                                            onClick={() => removeFromCartHandler(item.product)}
                                        >
                                            <i className='fas fa-trash'></i>
                                        </Button>
                                    </Col>
                                </Row>
                            </ListGroup.Item>
                        ))}
                    </ListGroup>
                )}
            </Col>
            <Col md={4}>
                <Card>
                    <ListGroup variant='flush'>
                        <ListGroup.Item>
                            <h2>Summary</h2>
                            
                        </ListGroup.Item>
                        <ListGroup.Item>
                            Item(s): {cartItems.reduce((acc, item) => acc + item.qty, 0)}
                            
                        </ListGroup.Item>
                        <ListGroup.Item>
                            Subtotal: ${(cartItems.reduce((acc, item) => acc + (item.price*item.qty), 0)).toFixed(2)}
                        </ListGroup.Item>
                        
                        <ListGroup.Item>
                            <Row className="hrzPadding">
                            <Button
                                type='button'
                                className="btn-block"
                                disabled={cartItems.length === 0}
                                onClick= {checkoutHandler}
                                >
                                Proceed To Checkout
                            </Button>
                            </Row>
                            
                        </ListGroup.Item>
                    </ListGroup>
                </Card>
            </Col>
        </Row>
    );
}

export default CartScreen;
